/* 장바구니 리스트 CSS */

/* 전체부모 박스 */
#cartlist {
    position: fixed;
    top: 0px;
    right: -60%;
    width: 60vw;
    height: 100vh;
    background-color: rgba(255, 255, 255, 0.8);
    z-index: 99999999;
}

/* 닫기버튼 */
#cartlist .cbtn {
    position: absolute;
    top: 5%;
    right: 5%;
    width: 40px;
    height: 39px;
    /* background: url(../../public/images/mv_btns.png) no-repeat; */
    background-position: -129px -342px;
    transition: transform 0.3s;
}

#cartlist .cbtn span {
    /*  */
    font-size: 2.5rem;
}

#cartlist table {
    width: 90%;
    margin: 50px auto;
    font-size: 14px;
    border-top: 2px solid rgb(34, 34, 34);
    border-bottom: 2px solid rgb(34, 34, 34);
    border-collapse: collapse;
}

#cartlist caption {
    padding: 20px 0px;
    text-decoration: underline wavy;
    font-size: 30px;
}

#cartlist th {
    padding: 15px 0px;
    background-color: rgb(229, 229, 229);
    font-size: 16px;
}

#cartlist td {
    padding: 10px 0px;
    border-top: 1px solid rgb(85, 85, 85);
    text-align: center;
}
#cartlist td:nth-child(3) {
    font-size: 1.2rem;
}
#cartlist td img {
    width: 50px;
}

/* 카트버튼 이미지 */
#mycart {
    position: fixed;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    z-index: 999999;
    cursor: pointer;
    display: none;
}
/* 카트박스 사이드 이동상태 */
#mycart.on {
    position: fixed;
    top: 50%;
    left: calc(100% - 80px);
    transform: translate(0, -50%);
    background-color: #ccc;
    border-radius: 10px 0 0 10px;
    padding: 15px;
    transition: .4s ease-in 2s;
    border: 3px solid #555;
}

/* 원본 이미지 크기 */
#mycart h1 {
    font-size: 8rem;
    border-radius: 20px;
}

/* 변경 이미지 크기 */
#mycart.on h1 {
    font-size: 2.2rem;
    transition: 0.4s ease-in 2s;
}

#mycart .cntBx {
    text-align: center;
    font-size: 20px;
    font-weight: bold;
}
